@import url(https://fonts.googleapis.com/css2?family=Poppins:ital,wght@1,300&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App {
  text-align: center;
}

body {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100vh;
  background-color: #191b20;
}

#quote-box {
  background-color: #b68bb9;
  color:  #191b20;
  /* border: #191b20 3px solid; */
  position: relative;
  border-radius: 10px;
  width: 500px;
  padding: 40px;
}

#text {
  font-size: x-large;
  font-family: 'Poppins', sans-serif;
  text-align: justify;
}

#author {
  font-size: medium;
  font-family: 'Poppins', sans-serif;
  text-align: right;
}

#button-wrapper {
  display:flex;
  -webkit-columns: 3;
          columns: 3;
}

.row {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  width: 100%;
}

.column {
  display: flex;
  flex-direction: column;
  flex-basis: 100%;
  flex: 1 1;
}

#tweet-quote {
  font-family: 'Poppins', sans-serif;
  font-size: 16px;
  border-radius: 5px;
  background-color: #191b20;
  color: #b68bb9;
  text-decoration: none;
  height: 24px;
}

#new-quote {
  font-family: 'Poppins', sans-serif;
  font-size: 16px;
  border-radius: 5px;
  background-color: #191b20;
  color: #b68bb9;
  /* background: none!important; */
  border: none;
  padding: 0!important;
  height: 24px;
  cursor: pointer;
  outline: none;
}

